export default {
	data() {
		return {
			staff: []
		}
	},
	onLoad() {
		this.__init()
	},
	methods: {
		async __init() {
			this.$H.post("/agent/", {
				user_id: uni.getStorageSync('uid'),
				token: uni.getStorageSync('utoken'),
				opt: "user_list"
			}).then((res) => {
				console.log(res);
				this.staff = res
				
			}).catch(() => {
				console.log("catch error!!");
			})
		}
	}
}
